@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.astudio__header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px;
  width: 100%;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.2);
  position: fixed;
  background: #ffffff;
  top: 0px;
  z-index: 501;
}
@media (min-width: 1025px) {
  .astudio__header-container .astudio__site-logo {
    position: absolute;
    left: 6.6rem;
  }
}
.astudio__header-container .astudio__menu-link {
  display: none;
  position: absolute;
  top: 1.4rem;
  left: -0.9rem;
  height: 100vh;
  width: 100vw;
  background: #ffffff;
  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.12);
  z-index: 501;
  list-style: none;
}
.astudio__header-container .astudio__menu-link-li {
  margin-bottom: 20px;
}
.astudio__header-container .astudio__menu-link-a {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  letter-spacing: 0.1em;
  text-decoration: none;
}
.astudio__header-container .astudio__menu-link-a:hover {
  text-decoration: none;
}
@media (min-width: 1025px) {
  .astudio__header-container .astudio__menu-link {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    margin-top: 26px;
  }
  .astudio__header-container .astudio__menu-link-li {
    margin-right: 20px;
    margin-bottom: 0;
  }
  .astudio__header-container .astudio__menu-link-a {
    font-family: "Averta Demo PE Cutted Demo";
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
    letter-spacing: 0.1em;
    text-decoration: none;
  }
  .astudio__header-container .astudio__menu-link-a:hover {
    text-decoration: none;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__header-container .astudio__menu-link {
    display: none;
    position: absolute;
    left: 23rem;
    top: 20px;
    width: 60vw;
    height: 90vh;
    background: #ffffff;
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.12);
    z-index: 9;
    padding: 50px;
  }
  .astudio__header-container .astudio__menu-link-li {
    margin-bottom: 30px;
  }
  .astudio__header-container .astudio__menu-link-a {
    font-family: "Averta Demo PE Cutted Demo";
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #000000;
    color: #000000;
  }
}
@media (min-width: 1025px) {
  .astudio__header-container .astudio__menu-link {
    position: static;
    display: flex;
    height: 0;
    width: 100%;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__header-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 14px 15px 14px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.2);
    position: fixed;
    background: #ffffff;
    top: 40px;
    z-index: 501;
  }
}
@media (min-width: 1025px) {
  .astudio__header-container {
    display: block;
    box-shadow: none;
    position: unset;
  }
}
.astudio__header-container .hamburger {
  background-color: #2639ed;
  border: none;
  background-image: url(".../../../../../images/hamburger.png");
  padding: 22px;
  background-repeat: no-repeat;
  background-size: 24px;
  background-position: 50%;
  border-radius: 5px;
  cursor: pointer;
}
@media (min-width: 1025px) {
  .astudio__header-container .hamburger {
    display: none;
  }
}